.tap-popup-wrapper {
		position: fixed;
	    right: -348px;
    bottom: -327px;
	border-radius: 50%;
	z-index: 999;
	width: 800px;
	height: 800px;
	pointer-events: none;

	&.open {
		width: 800px;
		height: 800px;
		pointer-events: all;
		.tap-popup-wave {
			&:before {
				transform: scale(1);
			}

			&:after {
	    animation: pulse-animation 1s cubic-bezier(0.24, 0, 0.38, 1) infinite;
	    transition: opacity .3s, transform .3s, visibility 0s 1s;
			}
		}
	}
}

@keyframes pulse-animation {
	from {
		transform: scale(0);
		opacity: 0.75;
	}

	to {
		transform: scale(1.6);
		opacity: 0;
	}
} 


.tap-popup {
	  position: absolute;
    font-size: 1rem;
    border-radius: 50%;
    background-color: @accent;
    box-shadow: 0 20px 20px 0 rgba(0,0,0,0.14), 0 10px 50px 0 rgba(0,0,0,0.12), 0 30px 10px -20px rgba(0,0,0,0.2);
    width: 100%;
    height: 100%;
    opacity: 0;
    transform: scale(0);
    transition: transform 0.3s cubic-bezier(0.42, 0, 0.58, 1),opacity 0.3s cubic-bezier(0.42, 0, 0.58, 1);
   
}


.tap-popup-wrapper.open .tap-popup {
    transform: scale(1);
    opacity: .95;
    transition: opacity 0.3s cubic-bezier(0.42, 0, 0.58, 1),-webkit-transform 0.3s cubic-bezier(0.42, 0, 0.58, 1);
    transition: transform 0.3s cubic-bezier(0.42, 0, 0.58, 1),opacity 0.3s cubic-bezier(0.42, 0, 0.58, 1);
}

.tap-popup-wave {
	position: absolute;
    border-radius: 50%;
    z-index: 10001;
    top: 344px;
    left: 344px;
    width: 112px;
    height: 112px;
	.btn-control {
		top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    z-index: 10002;
    position: absolute;
    color: @white;
	}

	&:before {
		content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: @white;
    transform: scale(0);
    transition: transform .3s;
	}

	&:after {
		content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: @white;
    z-index: -1;
    transform: scale(0);
	}
}


	.tap-popup__content{
		width: 456px;
    height: 400px;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    padding: 20px;
    padding-left: 10px;
    vertical-align: bottom;
    position: relative;
    display: table-cell;
}

.form-group {
	position: relative;
	max-width: 225px;
 
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  display: flex;
  align-items: center;
  justify-content: center;

	  label {
	    color: @accent;
	    position: absolute;
	    top: 50%;
	    left: 50%;
	    font-size: 1rem;
	    font-weight: 600;
	    cursor: text;
			font-family: 'Nunito', sans-serif;
	    transition: color .2s ease-out,-webkit-transform .2s ease-out;


	    transition: transform .2s ease-out,color .2s ease-out;
	    transform-origin: 0 0;
	    text-align: initial;
	    transform: translate(-50%, -50%);
	    z-index: 2;
	    white-space: nowrap;

			  &.active-label {
		    transform: scale(.6) translate(-50%, -36px);
		    transform-origin: 0 0;

		    color: @teal;
		}

				&.active-label_g {
		    transform: scale(.6) translate(-50%, -36px);
		    transform-origin: 0 0;
		    color: @font-color;
		}
	}
}

.text-field {
    background: @white;
    max-width: 225px;
    letter-spacing: .05rem;
    font-size: 15/16 * 1rem;
    font-weight: 600;
    padding: 14px 18px 14px 32px;
    text-align: left;
    border-radius: 30px;
    border: 2px solid @teal;
    color: @font-color;
    outline: 0;
    width: 100%;
    transition: .25s;
    cursor: pointer;
    font-family: 'Nunito', sans-serif;
}


.tap-popup__btn {
	max-width: 225px;
	.btn-text {
		display: flex;
		align-items: center;
		justify-content: center;
		i {
			margin-left: 10px;
		}
	}
}

.tap-popup__form {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}



.form-group-wrap {
		position: relative;
		 margin-bottom: 20px;
		.tap-popup__form-ico {
		position: absolute;
		left: 5px;
		top: 50%;
		transform: translateY(-50%);
		z-index: 15;
		color: @accent;
		transition: color 0.25s ease;
	}
}