.header {
	height: 64px;
	line-height: 64px;
	position: fixed;
	width: 100%;
	z-index: 99;
	font-weight: 700;
	font-family: 'Nunito', sans-serif;
	&.active {
		.header__layer {
			background: @accent;
			border-color: @accent;

		}
		.header__top-nav-link {
			color: @white;
		}

		.main-btn {
			background: @white;
			color: @accent;
			&:hover {
      	color: @white;
    	}
			& .btn-overlay {
				&:after {
					background-color: @teal;
				}
			}
		}
	}
}


.header__layer {
	background: linear-gradient(135deg, @teal, @main-color);
	
	border-color: @main-color;
	box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.12), 0px 4px 5px 0px rgba(0, 0, 0, 0.08), 0px 1px 10px 0px rgba(0, 0, 0, 0.06);
	transition: background 0.25s linear;
}

.header__top {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
}

.header__top-logo {
	flex: 0 1 33%;
	padding: 0 15px;
	display: flex;
	justify-content: center;
	img {
		max-width: 125px;
	}
}

.header__top-btn {
	flex: 0 1 33%;
	padding: 0 15px;
}

.header__top-nav {
	flex: 0 1 33%;
	display: flex;
	justify-content: flex-end;
}

.header__logo-ico {
	// max-width: 65px;
	display: flex;
	align-items: center;
	justify-content: center;
	svg {
		max-width:85px;
		max-height: 45px;
	}
}


.header__top-nav-list {
	display: flex;
	justify-content: space-between;
}

.header__top-nav-list-item {
	margin: 0 10px;
}

.header__top-nav-link {
	color: @white;
}
















